.navigation {
    display: flex;
    justify-content: space-evenly;
    font-weight: 700;
    margin: 0 30%;
    cursor: pointer;
    font-size: 1.5em;
}

.navigation span {
    min-width: 55px;
}

.container {
    display: flex;
}

section {
   flex: 1;
   max-width: 45%;
   display: flex;
   flex-direction: column;
   margin: 0 2%;
}

.title {
    text-align: center;
    font-size: 1.5em;
    font-weight: 500;
}